@import url(https://fonts.googleapis.com/css2?family=Akaya+Telivigala&family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Akaya+Telivigala&family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Acme&family=Akaya+Telivigala&family=Lobster&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  font-family: 'Akaya Telivigala', cursive;
}
.box{
  width: 100vw;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding-top: 30px;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.add-box{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  min-width: 450px;
  min-height: 400px;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: burlywood;
  border-radius: 20px;
  border-radius: 50px;
  background: #e0e0e0;
  box-shadow:  34px 34px 68px #bebebe,
             -34px -34px 68px #ffffff;
}
.entry-element{
  display: -webkit-flex;
  display: flex;
}
.main-icon{
  font-size: 30px;
  padding-left: 15px;
  display: inline-block;
  cursor: pointer;
  margin-top: 5px;
  color: green;
}
input{
  display: inline-block;
  height: 35px;
  font-size: 20px;
}
.g-content,.div-content{
  padding-top: 20px;
  cursor: pointer;
  font-size: 25px;
  color: darkslategrey;
}
.div-content:last-child{
  padding-bottom: 30px;
}
.add-bar{
  text-align: center;
}
.del{
  color: red;
}
.main{
  font-family: 'Abril Fatface', cursive;
}
.entr{
  font-family: 'Abril Fatface', cursive;
  color: darkslategrey;
}
